import React from 'react';
import clip from './clip.png';
import blurred from './blurred.jpg';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import LightBulb from './LightBulb';

const tilts = ["tilt-left", "tilt-right", "tilt-left extra", "tilt-right extra", "tilt-left slight", "tilt-right slight"];
const lightOn = [true, false];

export default class PhotoHanger extends React.Component {
    constructor(props, context) {
        super(props, context);
        this.state = {
            n: 20,
            tilts: [],
            isOn: []
        }
        this.setTilts();
    }

    componentDidMount() {
        // setInterval(() => this.updateBulbStates(), 500);
    }

    updateBulbStates = () => {
        const isOn = [...Array(this.state.n).keys()].map(() => lightOn[this.randomInteger(2)]);
        this.setState({isOn: isOn});
    }

    setTilts = () => {
        const _tilts = [...Array(this.state.n).keys()].map(() => tilts[this.randomInteger(6)]);
        setTimeout(() => {
            this.setState({tilts: _tilts});
        }, 400);
    }

    randomInteger = (range) => {
        return parseInt(Math.random() * range);
    }

    render(){
        return (
            <Row className="photo-hanger">
                {[...Array(this.state.n).keys()].map((i) => {
                    return (
                        <React.Fragment>
                            <Col className="photo-card-wrapper" xs={2}>
                                <img alt="" className="clip" src={clip} />
                                <div className={`photo-card ${this.state.tilts[i]}`}>
                                    <div className="photo">
                                        <img alt="" src={blurred} />
                                    </div>
                                    <div className="greetings">
                                        {i==0 && <button>Upload</button>}
                                    </div>
                                </div>
                            </Col>
                            {
                                i!==this.state.n-1 && (i+1)%5!=0 && (
                                    <LightBulb margin={60} isOn={this.state.isOn[i]}/>
                                )
                            }
                        </React.Fragment>
                    );
                })}
            </Row>
        );
    }  
};